	},
	methods: {
		// mescroll组件初始化的回调,可获取到mescroll对象
		mescrollInit(mescroll) {
			this.mescroll = mescroll;
		},
		/*上拉加载的回调*/
		upCallback(mescroll) {
			// 此时mescroll会携带page的参数:
			let pageNum = mescroll.num; // 页码, 默认从1开始
			let pageSize = mescroll.size; // 页长, 默认每页10条
			mescroll.endByPage(this.list.length, this.totalPage);
			//设置列表数据
			if (mescroll.num == 1) this.dataList = []; //如果是第一页需手动置空列表
			this.dataList = this.dataList.concat(this.list); //追加新数据
		},
		load() {
			uni.createSelectorQuery().selectAll('.lazy').boundingClientRect((images) => {
				images.forEach((image, index) => {
					if (image.top <= this.windowHeight) {
						setTimeout(() => {
							this.dataList[image.dataset.index].show = true;
						}, 200)
					}
				})
			}).exec()
		},
		imageLoad(e) {
			this.dataList[e.target.dataset.index].loaded = true
		},
		areas() {
			this.$refs.picker.show();
		},
		onConfirm(val) {
			console.log(val);
			this.address = val.checkArr[1]
		}
	},
	// 必须注册滚动到底部的事件,使上拉加载生效
	onReachBottom() {
		this.mescroll && this.mescroll.onReachBottom();
	},
	onLoad() {
		this.windowHeight = uni.getSystemInfoSync().windowHeight
	},
	onShow() {
		if (!this.show) {
			this.show = true
			setTimeout(() => {
				this.load()
			}, 500)
		}
	},
	onPageScroll() {
		this.load()
	}
}
